import React, {createContext, useState} from "react";
import * as AppLocalStorage from "./AppLocalStorageKeys";
import Biblija from '../data/Biblija.json';
import Knjige from '../data/Books.json';

const VersesContext = createContext([]);

export function VersesProvider({ children }) {
    const [verses, setVerses] = useState([]);
    const [randomVerse, setRandomVerse] = useState([]);
    const [bookVerses, setBookVerses] = useState([]);
    const [bookChapters, setBookChapters] = useState([]);

    const biblija = Biblija.biblija;
    const books = Knjige.books;
    const testaments = Knjige.testaments;

    const getSavedTestament = () => {
        return AppLocalStorage.get("testament") || "new";
    };
    const [testament, setTestament] = useState(getSavedTestament());

    const handleTestament = (testament) => {
        setTestament(testament);
        AppLocalStorage.set("testament", testament)
    };

    const getRandomVerse = () => {
        const selTestament = testaments.filter((item) => (item.testament === testament))[0];
        const { last_id, first_id } = selTestament;
        const indexVerse = Math.floor((Math.random() * (last_id - first_id + 1)) + first_id);
        const bibleVerse = biblija.filter(verse => (Number(verse._id) === indexVerse))[0];
        const hrvBookName = (books.filter((item) => (item.eng_short === bibleVerse.book))[0]).name;
        bibleVerse.name = hrvBookName;
        setRandomVerse(bibleVerse); 
    }

    const searchVerses = (sstring) => {
        const stringVariants = [
            ' '.concat(sstring), 
            '“'.concat(sstring),
            '”'.concat(sstring),
            '\''.concat(sstring)
        ];
        let foundVerses = [];

        stringVariants.forEach((oneString) => {
            let privFound = [];
            privFound = biblija.filter(verse => {
                                const { verse_text } = verse;
                                return verse_text.includes(oneString);
                            });
            foundVerses.push(...privFound);
        });
        setVerses(foundVerses);
    };

    const getBookVerses = (book) => {
        setBookVerses(biblija.filter((item) => (item.book === book) ))
    }

    const getBookChapters = (book) => {
        const noOfChapters = Number(books.filter(bookTitle => (bookTitle.eng_short === book))[0].chapters);
        let chapterArray = [];
        for (let i = 1; i <= noOfChapters; i++) {
                chapterArray.push(i);
            }
        setBookChapters(chapterArray);
    }

    return(
        <VersesContext.Provider value={
            {
                biblija,
                testaments,
                books,
                verses,
                searchVerses,

                randomVerse,
                getRandomVerse,

                testament,
                handleTestament,

                getBookVerses,
                bookVerses,

                bookChapters,
                getBookChapters
            }
        }>
            {children}
        </VersesContext.Provider>
    )
}

export default VersesContext;