import React, {lazy, Suspense} from "react";
import {BrowserRouter, Route, Routes} from "react-router-dom";
import Home from "./components/Home";

const OverlayMenu = lazy(() => import("./components/OverlayMenu"));
const About = lazy(() => import("./components/About"))
const Settings = lazy(() => import("./components/Settings"))
const SearchTerm = lazy(() => import("./components/SearchTerm"))
const BookChapterVerse = lazy(() => import("./components/BookChapterVerse"))

const AppRoutes = () => {
    return(
        <div>
            <BrowserRouter>
                <Suspense fallback={<div>Učitavam...</div>}>
                    <OverlayMenu />
                    <Routes>
                        <Route path="/" element={<Home slide={0} />}/>
                        <Route path="/toc" element={<Home slide={1} />}/>
                        <Route path="/settings" element={<Settings />}/>
                        <Route path="/about" element={<About />}/>
                        <Route path="/book/:book" element={<BookChapterVerse />}/>
                        <Route path="/search" element={<SearchTerm />}/>
                        <Route path="/verse/:book/:chapter/:verse_id" element={<BookChapterVerse />}/>
                        <Route path="*" element={<Home slide="0" />}/>
                    </Routes>
                </Suspense>
            </BrowserRouter>
        </div>
    )
}

export default AppRoutes;