import React, {createContext, useEffect, useState} from "react";
import * as AppLocalStorage from "./AppLocalStorageKeys";

const FontSizeSettingContext = createContext([]);

export function FontSizeSettingProvider({ children }) {
    const getSavedFontSize = () => {
        return AppLocalStorage.get("theFontSize") || "fontZoomL";
    };

    const [theFontSize, setTheFontSize] = useState(getSavedFontSize());

    function handleFontSize(theFontSize) {
        setTheFontSize((prevFontSize)  => {
            document.body.classList.remove(prevFontSize);
            return theFontSize;
        });
        AppLocalStorage.set("theFontSize", theFontSize)
    };

    useEffect(() => {
        document.body.classList.add(theFontSize);
    }, [theFontSize]);

    return (
    <FontSizeSettingContext.Provider value={{
        theFontSize: theFontSize,
        handleFontSize: handleFontSize
    }}>
      {children}
    </FontSizeSettingContext.Provider>
  );
}

export default FontSizeSettingContext;
