import React, {createContext, useEffect, useState} from "react";
import * as AppLocalStorage from "./AppLocalStorageKeys";

const ThemeSettingContext = createContext([]);

export function ThemeSettingProvider({ children }) {
    const getSavedTheme = () => {
        return AppLocalStorage.get("theme") || "dayTheme";
    };

    const [theme, setTheme] = useState(getSavedTheme());

    function handleTheme(theme) {
        setTheme((prevTheme)  => {
            document.body.classList.remove(prevTheme);
            return theme;
        });
        AppLocalStorage.set("theme", theme)
    };

    useEffect(() => {
        switch (theme) {
            case "nightTheme":
                document.body.classList.add("nightTheme");
                break;
            case "sepiaTheme":
                document.body.classList.add("sepiaTheme");
                break;
            default:
                document.body.classList.add("dayTheme");
                break;
        }
    }, [theme]);

    return (
    <ThemeSettingContext.Provider value={{
        theme: theme,
        handleTheme: handleTheme
    }}>
      {children}
    </ThemeSettingContext.Provider>
  );
}

export default ThemeSettingContext;
