import React, { useEffect, useContext, useRef } from "react";
import { Link } from "react-router-dom";
import VersesContext from "../contexts/BibleContext";
import Cross from "../assets/covercross.webp";
import TableOfContent from "./TableOfContent";
import RandomVerse from "./RandomVerse";

import { Swiper, SwiperSlide, useSwiper } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "./swiper_styles.css";
import { Pagination, Navigation } from "swiper";


function Home(props) {
    const { getRandomVerse } = useContext(VersesContext);
    const hasTestamentData = useRef(false);
    const slideIndex = Number(props.slide);

    const SwiperPosition = () => {
        const swiper = useSwiper();
        // Get from the Bible only selected book (and the chapter)
        useEffect(() => {   
                    //Move slide
                    swiper.slideTo(slideIndex);
                }
        , [swiper])
        
        return(<></>)
    }


    useEffect(() => {
        if (!hasTestamentData.current) {
                getRandomVerse();
            } 
            hasTestamentData.current=true; 
    }, [getRandomVerse])


    return (
        <div>
        <Swiper
                pagination={{
                    type: "custom",
                }}
                navigation={true}
                modules={[Pagination, Navigation]}
                onSlideChange={() => {
                    window.scrollTo(0, 0);
                }}
                // onSwiper={(swiper) => console.log(swiper)}
            
                className="mySwiper"
            >
            <SwiperPosition />
                <SwiperSlide>
                    <div className="coverpage">
                        <div className="booktitle">
                            <Link to="/toc">
                                <p style={{
                                        fontFamily: "FrederickatheGreat-Regular",
                                        fontSize: "4rem"
                                    }}>
                                    BIBLIJA
                                </p>
                                <p>
                                    SVETO PISMO
                                </p>
                                <small>
                                    STAROGA I NOVOGA ZAVJETA
                                </small>
                            </Link>
                        </div>

                        <Link to="/toc">
                            <img width="240" height="389" id="cross-logo" src={Cross} alt="Cross" />
                        </Link>

                        <RandomVerse />

                    </div>
                </SwiperSlide>
                <SwiperSlide >
                    <TableOfContent />
                </SwiperSlide>
            </Swiper>
            
        </div>
    )
}

export default Home;