import { 
    EmailShareButton,
    FacebookShareButton,
    TwitterShareButton,
    ViberShareButton,
    WhatsappShareButton } from "react-share";

import {
    EmailIcon,
    FacebookIcon,
    TwitterIcon,
    ViberIcon,
    WhatsappIcon
    } from "react-share";

const SocialNetLinks = (props) => {

    const {shareUrl, verseLocationAndText, twitterVerseLocation, title} = props;

    return (
        <div className="sharelinks">

        <WhatsappShareButton
            url={shareUrl}
            title={verseLocationAndText}
            separator=":: "
        >
            <WhatsappIcon size={24} round />
        </WhatsappShareButton>

        <ViberShareButton
            url={shareUrl}
            title={verseLocationAndText}
        >
            <ViberIcon size={24} round />
        </ViberShareButton>

        <EmailShareButton
            disabled={false} 
            url={shareUrl}
            subject={title}
            body={verseLocationAndText}
        >
            <EmailIcon size={24} round={true} />
        </EmailShareButton>

        <FacebookShareButton
            disabled={false} 
            url={shareUrl}
            quote={verseLocationAndText}
        >
            <FacebookIcon size={24} round={true} />
        </FacebookShareButton>

        <TwitterShareButton
            url={shareUrl}
            title={title}
            hashtags={[twitterVerseLocation]}
        >
            <TwitterIcon size={24} round />
        </TwitterShareButton>

        </div>
    )
}

export default SocialNetLinks;