import { useContext } from "react";
import { Link } from "react-router-dom";
import VersesContext from "../contexts/BibleContext";
import SocialNetLinks from "./SocialNetLinks";

function RandomVerse(props) {
    const shareUrl = "https://skrilic.github.io/Biblija-hrv.android/";
    const title = "Citat iz Biblije";

    const { biblija, books, testament, testaments } = useContext(VersesContext);

    const tstm = testaments.filter((tst) => (tst.testament===testament))[0];

    const rndIndex = Math.random() * (tstm.last_id - tstm.first_id + 1) + tstm.first_id;
    const verse = biblija[Math.floor(rndIndex)];

    const theBook = books.filter(item => ( item.eng_short === verse.book ))[0];
    
    // const verseLocation = `${theBook.name} ${verse.chapter}:${verse.verse_no}`;
    const twitterVerseLocation = `${theBook.eng_short}_${verse.chapter}_${verse.verse_no}`;
    const verseLocationAndText = "\"" + verse.verse_text + "\" \n\t ~" + theBook.name + " " + verse.chapter + ":" + verse.verse_no + "\n\n";

    return (
        <div className="randomverse">
            
            <Link 
                
                to={`/verse/${theBook.eng_short}/${verse.chapter}/${verse.verse_no}`}
            >
                <p>{verse.verse_text}</p>
            </Link>
            
            <p className="bibleposition"> ~ {theBook.name} <em>{verse.chapter}:{verse.verse_no}</em></p>
            
            <SocialNetLinks
                shareUrl={shareUrl}
                title={title}
                verseLocationAndText={verseLocationAndText} 
                twitterVerseLocation={twitterVerseLocation}
            />        
        </div>
    )
}

export default RandomVerse;